import { FieldValues, UseFormReturn } from '@wix/patterns/form';
import { EntityPageMoreActions } from './actions/entityPageActions';
import { BaseCollectionConfig } from './types';

export type EntityPageConfig = {
  route: {
    path: string;
    params: {
      id: string;
    };
  };
  title?: {
    text: string;
  };
  subtitle?: {
    text: string;
    learnMore?: {
      url: string;
      label?: string;
    };
  };
  parentPageId?: string;
  layout?: {
    main: CardLayout[];
    sidebar?: CardLayout[];
  };
  moreActions?: EntityPageMoreActions;
} & BaseCollectionConfig;

export interface CardLayout {
  type: 'card';
  card: {
    title: {
      text: string;
    };
    subtitle?: {
      text: string;
    };
    children: LayoutContent[];
  };
}

export type LayoutContent =
  | FieldContent
  | ContainerContent
  | ComponentItemContent;

interface FieldContent {
  type: 'field';
  field: {
    span?: number;
    fieldId: string;
  };
}

interface ContainerContent {
  type: 'container';
  container: {
    span?: number;
    children: LayoutContent[];
  };
}

interface ComponentItemContent {
  type: 'component';
  component: {
    span?: number;
    componentId: string;
  };
}

export interface CustomComponentProps {
  entity?: Record<string, any>;
  form: UseFormReturn<FieldValues, any, undefined>;
}
